import React, { Component } from "react";

class About extends Component {
  render() {
    if (this.props.data) {
      var name = this.props.data.name;
      var profilepic = "images/" + this.props.data.image;
      var bio = this.props.data.bio;
      var phone = this.props.data.phone;
      var email = this.props.data.email;
      var resumeDownload = this.props.data.resumedownload;
    }

    return (
      <section id="about">
        <div>
          <div>
            <img className="profile-pic" src={profilepic} alt="Ziad" />
          </div>
          <div>
            <h1>Hello!</h1>
            <h2>Brief</h2>
            <p>
              My name is Ziad, I am an AWS enthusiast and a New Media artist/Creative coder,<br />
              currently working as a Technical Team Lead at Trukto building serverless applications on AWS.<br />
              I've been creative coding since 2016 and I've been collaborating as a programmer and visual artist<br/>
              with local and international sound artists / performers / visual artists since then.
              </p>
              <h2>
                About The Website
              </h2>
              <p>
                This website is built using ReactJS using a component based architecture.<br/>
                It is built on the <a href="https://github.com/facebook/create-react-app">create-react-app</a> boilerplate and it utilizes states<br/>
                in order to load and pass a json file to these components.<br/><br/>
                I work on the website as sprints, so expect regular updates and more stuff coming soon!
              </p>
            <div>
              <div>
                <h2>Get in touch!</h2>
                <p>
                  <span>{phone}</span>
                  <br />
                  <span>{email}</span>
                </p>
              </div>
            </div>
          </div>
        </div>

        <div>
          <div className="bird-container">
            <div className="bird" />
          </div>
        </div>
      </section>
    );
  }
}

export default About;
