import React, { Component } from "react";
import ReactGA from "react-ga";
import $ from "jquery";
import "./App.css";
import Header from "./Components/Header";
import Footer from "./Components/Footer";
import About from "./Components/About";
import Resume from "./Components/Resume";
import Contact from "./Components/Contact";
import Portfolio from "./Components/Portfolio";
import { Route, NavLink, Switch } from "react-router-dom";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { Redirect } from 'react-router';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foo: "bar",
      resumeData: {}
    };

    ReactGA.initialize("UA-133243650-1");
    ReactGA.pageview(window.location.pathname);
  }
  getResumeData() {
    $.ajax({
      url: "/resumeData.json",
      dataType: "json",
      cache: false,
      success: function(data) {
        this.setState({ resumeData: data });
      }.bind(this),
      error: function(xhr, status, err) {
        console.log(err);
        alert(err);
      }
    });
  }

  componentDidMount() {
    this.getResumeData();
  }

  render() {
    return (
      <div className="App">
        <div className="nav-wrap">
        <Redirect to="/frontpage"/>
          <nav id="nav-wrap">
            <ul id="nav" className="nav">
              <li>
                <NavLink exact to="/" activeClassName="active" className ="inactive">
                  Home
                </NavLink>
              </li>
              <li>
                <NavLink exact to="/about" activeClassName="active" className ="inactive">
                  About
                </NavLink>
              </li>
             
              <li>
                <NavLink exact to="/Portfolio" activeClassName="active" className ="inactive">
                Portfolio
                </NavLink>
              </li>
            </ul>
          </nav>
        </div>
        <Route render={({location})=>(
          <TransitionGroup>
            <CSSTransition
              key={location.key}
              timeout={300}
              classNames="fade"
              >
              <Switch location={location}>
                <Route exact path="/" render={(props)=><Header data={this.state.resumeData.main}/>}/>
                <Route exact path="/about" render={(props)=><About data={this.state.resumeData.main}/>}/>
                <Route exact path="/portfolio" render={(props)=><Portfolio data={this.state.resumeData.main}/>}/>
                <Route exact path="/frontpage" render={(props)=><Contact data={this.state.resumeData.main}/>}/>
              </Switch>
            </CSSTransition>
        </TransitionGroup>
        )}/>
        
        <Footer data={this.state.resumeData.main} />
      </div>
    );
  }
}

export default App;
