import React, { Component } from "react";
class Header extends Component {
  render() {
    if (this.props.data) {
      var name = this.props.data.name;
      var background = "/images/" + this.props.data.background;
      var backgroundm = "/images/" + this.props.data.backgroundm;
      var networks = this.props.data.social.map(function(network) {
        return (
          <li key={network.name}>
            <a href={network.url}>
              <i className={network.className} /> <br />
            </a>
          </li>
        );
      });
    }

    return (
      <header id="home">
        <div className="row banner">
          <video loop autoPlay id="myVideo">
            <source src={backgroundm} type='video/webm'/>
            <source src={background} type='video/mp4'/>
           Your browser does not support the video tag.
          </video>


          <div className="banner-text">
            <h1 className="responsive-headline">{name}.</h1>
            <p> AWS Enthusiast  · Technical Team Lead · New Media Artist · Systems Analyst</p>
            <hr/>
            <ul className="social">{networks}</ul>
          </div>
        <p id="smaller"><small>The background is from my performance at <a href="https://www.facebook.com/events/598906380470365/permalink/600181180342885/">MP V</a> Audio by <a href="https://soundcloud.com/0n4b">0N4B</a></small></p>
        </div>
      </header>
    );
  }
}

export default Header;
